          <div class="form-group">
            <label for="username">Username</label>
            <input type="text" class="form-control" id="username" v-model="form.username">
            <div class="field-error" v-if="$v.form.username.$dirty">
              <div class="error" v-if="!$v.form.username.required">ÏïÑÏù¥ÎîîÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.</div>
              <div class="error" v-if="!$v.form.username.alphaNum">ÏïÑÏù¥ÎîîÎäî ÏòÅÎ¨∏ ÎåÄ, ÏÜåÎ¨∏Ïûê Î∞è Ïà´ÏûêÎ°ú ÏûÖÎ†•ÌïòÏÑ∏Ïöî.</div>
              <div class="error" v-if="!$v.form.username.minLength">ÏïÑÏù¥ÎîîÎäî ÏµúÏÜå {{$v.form.username.$params.minLength.min}} Í∏ÄÏûêÏûÖÎãàÎã§.</div>
              <div class="error" v-if="!$v.form.username.maxLength">ÏïÑÏù¥ÎîîÎäî ÏµúÎåÄ {{$v.form.username.$params.maxLength.max}} Í∏ÄÏûêÏûÖÎãàÎã§.</div>
            </div>
          </div>
          <div class="form-group">
            <label for="emailAddress">Email address</label>
            <input type="email" class="form-control" id="emailAddress" v-model="form.emailAddress">
            <div class="field-error" v-if="$v.form.emailAddress.$dirty">
              <div class="error" v-if="!$v.form.emailAddress.required">Ïù¥Î©îÏùº Ï£ºÏÜåÎäî ÌïÑÏàò ÏûÖÎãàÎã§.</div>
              <div class="error" v-if="!$v.form.emailAddress.email">Ïú†Ìö®Ìïú Ïù¥Î©îÏùº Ï£ºÏÜåÍ∞Ä ÏïÑÎãôÎãàÎã§.</div>
              <div class="error" v-if="!$v.form.emailAddress.maxLength">Ïù¥Î©îÏùº Ï£ºÏÜåÎäî ÏµúÎåÄ {{$v.form.emailAddress.$params.maxLength.max}} Í∏ÄÏûêÏûÖÎãàÎã§.</div>
            </div>
          </div>
          <div class="form-group">
            <label for="password">Password</label>
            <input type="password" class="form-control" id="password" v-model="form.password">
            <div class="field-error" v-if="$v.form.password.$dirty">
              <div class="error" v-if="!$v.form.password.required">ÏïîÌò∏Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.</div>
              <div class="error" v-if="!$v.form.password.minLength">ÏïîÌò∏Îäî ÏµúÏÜå {{$v.form.password.$params.minLength.min}} Í∏ÄÏûêÏûÖÎãàÎã§.</div>
              <div class="error" v-if="!$v.form.password.maxLength">ÏïîÌò∏Îäî ÏµúÎåÄ {{$v.form.password.$params.maxLength.max}} Í∏ÄÏûêÏûÖÎãàÎã§.</div>
            </div>
          </div>
          <button type="submit" class="btn btn-primary btn-block">Í≥ÑÏ†ï ÏÉùÏÑ±</button>
          <p class="accept-terms text-muted">‚ÄúÍ≥ÑÏ†ï ÏÉùÏÑ±‚ÄùÏùÑ ÎàÑÎ•¥Î©¥ <a href="#">ÏïΩÍ¥Ä</a> Î∞è <a href="#">Í∞úÏù∏ Ï†ïÎ≥¥ Î≥¥Ìò∏Ï†ïÏ±Ö</a>Ïóê ÎèôÏùòÌï©ÎãàÎã§.</p>
          <p class="text-center text-muted">Ïù¥ÎØ∏ Í≥ÑÏ†ïÏù¥ ÏûàÏúºÏã≠ÎãàÍπå? <router-link to="/loginpage">Î°úÍ∑∏Ïù∏</router-link></p>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import { required, email, minLength, maxLength, alphaNum } from 'vuelidate/lib/validators'
import registrationService from '@/services/registration'

export default {
  name: 'RegisterPage',
  data: function () {
    return {
      form: {
        username: '',
        emailAddress: '',
        password: ''
      },
      errorMessage: ''
    }
  },
  validations: {
    form: {
      username: {
        required,
        minLength: minLength(2),
        maxLength: maxLength(50),
        alphaNum
      },
      emailAddress: {
        required,
        email,
        maxLength: maxLength(100)
      },
      password: {
        required,
        minLength: minLength(6),
        maxLength: maxLength(30)
      }
    }
  },
  methods: {
    submitForm () {
      this.$v.$touch()
      if (this.$v.$invalid) {
        return
      }

      registrationService.register(this.form).then(() => {
        this.$router.push({ name: 'LoginPage' })
      }).catch((error) => {
        this.errorMessage = 'Failed to register user. ' + error.message
      })
    }
  }
}
</script>

<style lang="scss" scoped>
